@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
.logon-container{
    width:100%;
    max-width: 1120px;
    height: 100vh;
    margin:0 auto;

    display: flex;
    justify-content: space-between;
    align-items: center;

}
.logon-container section.form{
    width: 100%;
    max-width: 350px;
    margin-right: 30px;
}
.logon-container section.form form{
    margin-top: 100px;
}
.logon-container section form h1{
    font-size: 32px;
    margin-bottom: 32px;
    color:white;
}

@media screen and (max-width: 812px) {
    .logon-container{
        padding: 12px;
        height: 100vmax; /*preserva altura total no mobile*/
    }
    .logon-container section.form{
        margin-right: 0px;
        max-width: 100%;
        display:flex;
        flex-direction: column;
    }
    .logon-container .img-heroes{
        display: none;
    }
}
.profile-container{
    width: 100%;
    max-width: 1180px;
    padding: 0 30px;
    margin: 32px auto;
}
.profile-container header{
    display: flex;
    align-items: center;
}
.profile-container header span{
    font-size: 20px;
    margin-left: 24px;
    color:white;
}
.profile-container header img{
    height: 64px;
}
.profile-container header span{
    width: 260px;
    margin-left: auto;
    margin-top: 0;
}
.profile-container header button{
    width: 60px;
    height: 60px;
    border-radius: 4px;
    border: 1px solid #dcdce6;
    background: transparent;
    margin-left: 16px;
    transition:  border-color 0.2s;
}
.profile-container header button:hover{
    border-color: #999;
}
.profile-container h1{
    margin-top:40px;
    margin-bottom: 40px;
    color:white;
}
.profile-container ul{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 24px;
    list-style: none;
}
.profile-container ul li{
    background: #FFF;
    padding: 24px;
    border-radius: 8px;
    position: relative;
}
.profile-container ul li button{
    position: absolute;
    right: 24px;
    top: 24px;
    border: 0;
    background: #FFF;
}
.profile-container ul li button:hover{
    opacity: 0.8;
}
.profile-container ul li strong{
    display: block;
    margin-bottom: 16px;
    color: #41414d;
}
.profile-container ul li p + strong{
    margin-top: 32px;
}
.profile-container ul li p{
    color: #737380;
    line-height: 21px;
    font-size: 16px;
}
@media screen and (max-width: 812px) {
    .profile-container ul{
     
        grid-template-columns: 1fr;
       
    }
    .profile-container header{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        }
    .profile-container header img{
        
        grid-area: 1 / 1 / 2 / 2;

    }
    .profile-container header span{
        grid-area: 2 / 1 / 3 / 2;
        width: 100%;
    }
    .profile-container header a{
        grid-area: 1 / 2 / 2 / 3;
        justify-self: right;
    }
    .profile-container header button {
        margin-left:0;
    }


}
*{
    margin:0;
    padding:0;
    outline: 0;
    box-sizing: border-box;
}
body{
    font:400 14px Roboto, sans-serif;
    background: #7159c1;
    -webkit-font-smoothing: antialiased;
}
input, button, textarea{
    font:400 18px Roboto, sans-serif;
}
button{
    cursor: pointer;
}
form input,textarea{
    width: 100%;
    height: 60px;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 0 24px;
}
form textarea{
    min-height: 140px;
    padding: 16px 24px;
    line-height: 24px;
    resize: vertical;
}
.button,.simple-button{
    width: 100%;
    height: 60px;
    background: #59a4c1;
    border:0;
    border-radius:  8px;
    color: #FFF;
    font-weight: 700;
    margin-top: 16px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
    transition: -webkit-filter 0.2s;
    transition: filter 0.2s;
    transition: filter 0.2s, -webkit-filter 0.2s;

}
.simple-button{
    width: auto;
    padding: 0px 35px 0px 35px ;
    margin-bottom: 40px;
}
.button:hover{
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}
.back-link{
    display: flex;
    align-items: center;
    margin-top: 40px;
    color: #FFFFFF;
    font-size: 18px;
    text-decoration: none;
    font-weight:500; 
    transition: opacity 0.2s;

}
.back-link svg{
    margin-right: 8px;

}
.back-link:hover{
    opacity: 0.8;
}
.global-container{
    width:100%;
    max-width: 1120px;
    height: 100vh;
    margin:0 auto;

    display: flex;
    justify-content: center;
    align-items: center;
}
.global-container .content{
    width: 100%;
    padding: 96px;
    background: #7159c1;
    box-shadow: 0 0 100px rgba(0,0,0, 0.1);
    border-radius: 8px;

    display: flex;
    justify-content: space-between;
    align-items: center;
}
.global-container .content section{
    width: 100%;
    max-width: 380px;
}
.global-container .content section h1{
    margin: 64px 0 32px;
    font-size: 32px;
    color:#b0e9ff;

}
.global-container .content section p{
    font-size: 18px;
    color: #f6eeee;
    line-height: 32px;
}
.global-container .content form{
    width: 100%;
    max-width: 450px;
}

.global-container .content form input,
.global-container .content form textarea{
    margin-top:8px;
}
.global-container .content form .input-group {
    display: flex;
}
.global-container .content form .input-group input + input{
    margin-left: 8px;
}
@media screen and (max-width: 812px) {
    .global-container{
        height: auto;
        padding: 13px;
    }
    .global-container .content{
        flex-direction: column;
        padding: 5px;
        box-shadow: none;
    }
    .global-container .content section h1 {
        margin: 10px 0 10px;
        font-size: 32px;
    }
    .global-container .content img{
        width: 12em;
    }
    .back-link{
        margin-bottom: 40px;
    }
   
}
